import * as React from "react";
import Box from "@mui/material/Box";
import Grid from "@mui/material/Grid";
import Container from "@mui/material/Container";
import Typography from "../components/Typography";
import TextField from "../components/TextField";
import Snackbar from "../components/Snackbar";
import Button from "../components/Button";
import { styled } from "@mui/material/styles";

const ContainerImg = styled("div")`
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: url(/img/ctaback.jpg);
  background-size: cover;
`;

function ProductCTA() {
  const [open, setOpen] = React.useState(false);

  const handleSubmit = (event: React.FormEvent<HTMLFormElement>) => {
    event.preventDefault();
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  return (
    <ContainerImg sx={{ pt: 2, pb: 10 }}>
      <Container
        component="section"
        sx={{
          mt: 10,
          display: "flex",
        }}
      >
        <Grid container sx={{ justifyContent: "center" }}>
          <Grid item xs={12} md={6} sx={{ zIndex: 1 }}>
            <Box
              sx={{
                display: "flex",
                justifyContent: "center",
                bgcolor: "#377dff",
                py: 8,
                px: 3,
              }}
            >
              <Box
                component="form"
                onSubmit={handleSubmit}
                sx={{ maxWidth: 400 }}
              >
                <Typography  variant="h2" component="h2" gutterBottom sx={{ fontWeight: 700, color: "common.white" }}>
                  
                </Typography>
                <Typography variant="h5" sx={{  color: "common.white" }}>juwanstar@naver.com</Typography>
                <Typography
                  variant={"h6"}
                  gutterBottom
                  align={"left"}
                  sx={{  color: "common.white" }}
                >
                  010-9486-1040
                </Typography>
              </Box>
            </Box>
          </Grid>
        </Grid>
      </Container>
    </ContainerImg>
  );
}

export default ProductCTA;
