import * as React from "react";
import Box from "@mui/material/Box";
import Grid from "@mui/material/Grid";
import Link from "@mui/material/Link";
import Container from "@mui/material/Container";
import Typography from "../components/Typography";

function Copyright() {
  return (
    <React.Fragment>
      {"COPYRIGHT  2022 SPACETALK ALL RIGHS RESERVED. - "}
      <Link color="inherit" href="https://www.spacetalk.co.kr/">
        Your Website
      </Link>
      {". "}
      {new Date().getFullYear()}
    </React.Fragment>
  );
}

const iconStyle = {
  width: 48,
  height: 48,
  display: "flex",
  justifyContent: "center",
  alignItems: "center",
  backgroundColor: "warning.main",
  position: "relative",
  mr: 3,
  "&:hover": {
    bgcolor: "warning.dark",
  },
};

export default function AppFooter() {
  return (
    <Typography
      component="footer"
      sx={{
        display: "flex",
        bgcolor: "secondary.light",
      }}
    >
      <Container sx={{ my: 4 }}>
        <Grid
          sx={{
            position: "absolute",
            display: { xs: "none", sm: "none", md: "block" },
          }}
        >
          <Box
            component="img"
            src="https://pds.saramin.co.kr/company/logo/201505/19/1432028087_aa84-7a63cf10_logo.jpg"
            height="6vh"
            max-width="20vw"
            sizes="200px"
            alt="arrow down"
          />
        </Grid>
        <Grid
          container
          direction="row"
          justifyContent="center"
          wrap="wrap"
          sx={
            {
              // position: "absolute",
              // display: { xs: "none", sm: "none", md: "block" },
            }
          }
        >
          <Box
            component="a"
            href="https://www.instagram.com/spacetalk_landscape.design/"
            sx={iconStyle}
            // right="200px"
          >
            <img src="/img/instar.png" alt="Instar" />
          </Box>
          <Box
            component="a"
            href="https://blog.naver.com/space_talk"
            sx={iconStyle}
          >
            <img src="/img/blog.png" alt="Blog" />
          </Box>
        </Grid>

        <Box sx={{ my: 4 }}>
          <Typography variant="h5" marked="left">
            {":   \n 82 25 3,4"}
            <br />
            {": master@spacetalk.co.kr"}
            <br />
            {": "}{" "}
          </Typography>
        </Box>

        <Grid sx={{ my: 4 }}>
          <Copyright />
        </Grid>
      </Container>
    </Typography>
  );
}

