import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";

import Box from "@mui/material/Box";
import Typography from "@mui/material/Typography";

import Card from "@mui/material/Card";
import CardMedia from "@mui/material/CardMedia";
import CardContent from "@mui/material/CardContent";

import useMediaQuery from "@mui/material/useMediaQuery";
import { useTheme } from "@mui/material/styles";

const images = [
  {
    num: 6,
    url: "/img/spactack.png",
    title: "",
    tutor: [
      ["2020.04 ~  "],
      <br />,
      ["  1"],
      <br />,
      [" "],
      <br />,
      ["PM, "],
    ],
  },
  {
    num: 5,
    url: "/img/lift29.png",
    title: "29(10 )",
    tutor: [
      ["2018.10 ~ 2020.02"],
      <br />,
      [" "],
      <br />,
      [" "],
      <br />,
      ["PM, "],
    ],
  },
  {
    num: 4,
    url: "/img/kakaovx.png",
    title: "(smilegate)/vx()",
    tutor: [
      ["2016.05 ~ 2017.05"],
      <br />,
      ["  2"],
      <br />,
      [" "],
      <br />,
      ["PM & "],
    ],
  },
  {
    num: 3,
    url: "/img/gofzone.jpg",
    title: "",
    tutor: [
      ["2014.09 ~ 2016.04"],
      <br />,
      ["  1"],
      <br />,
      [" "],
      <br />,
      [", "],
    ],
  },
  {
    num: 2,
    url: "/img/eyeden.jpg",
    title: "",
    tutor: [
      ["2012.07 ~ 2014.09"],
      <br />,
      ["10   "],
      <br />,
      [" "],
      <br />,
      [", "],
    ],
  },
  {
    num: 1,
    url: "/img/mgame.png",
    title: "",
    tutor: [
      ["2010.02 ~ 2012.06"],
      <br />,
      [" MMORPG "],
      <br />,
      [" "],
      <br />,
      [", "],
    ],
  },
];

const ProductInterior = (): JSX.Element => {
  const theme = useTheme();

  const isMd = useMediaQuery(theme.breakpoints.up("md"), {
    defaultMatches: true,
  });

  const sliderOpts = {
    dots: true,
    arrows: false,
    infinite: true,
    slidesToShow: isMd ? 3 : 1,
    slidesToScroll: 1,
    autoplay: true,
    autoplaySpeed: 3000,
  };

  return (
    <Box>
      <Box marginBottom={4}>
        <Typography
          variant="h4"
          align={"center"}
          data-aos={"fade-up"}
          gutterBottom
          sx={{
            fontWeight: 700,
          }}
        >
          company
        </Typography>
      </Box>
      <Box maxWidth={{ xs: 320, sm: 420, md: 1 }} margin={"0 auto"}>
        <Slider {...sliderOpts}>
          {images.map((item, i) => (
            <Box key={i} padding={{ xs: 1, md: 2, lg: 3 }}>
              <Box
                display={"block"}
                width={1}
                height={1}
                // onClick={() => openLightbox(i)}
                sx={{
                  textDecoration: "none",
                  transition: "all .2s ease-in-out",
                  "&:hover": {
                    transform: `translateY(-${theme.spacing(1 / 2)})`,
                  },
                }}
              >
                <Box
                  component={Card}
                  width={1}
                  height={1}
                  display={"flex"}
                  flexDirection={"column"}
                  sx={{ backgroundImage: "none" }}
                >
                  <CardMedia
                    title={item.title}
                    image={item.url}
                    sx={{
                      position: "relative",
                      height: { xs: 80, sm: 80, md: 80 },
                      overflow: "hidden",
                    }}
                  ></CardMedia>
                  <CardContent>
                    <Typography
                      variant={"h6"}
                      gutterBottom
                      align={"left"}
                      sx={{ fontWeight: 700 }}
                    >
                      {item.title}
                    </Typography>
                    <Box display={"flex"} alignItems={"center"} marginY={2}>
                      <Typography variant={"subtitle1"} color="text.secondary">
                        {item.tutor}
                      </Typography>
                    </Box>
                  </CardContent>
                  <Box flexGrow={1} />
                </Box>
              </Box>
            </Box>
          ))}
        </Slider>
      </Box>
    </Box>
  );
};

export default ProductInterior;
