import React from "react";
import Box from "@mui/material/Box";
import Link from "@mui/material/Link";

interface Props {
  colorInvert?: boolean;
}

const TopNav = ({ colorInvert = false }: Props): JSX.Element => {
  return (
    <Box
      display={"flex"}
      justifyContent={"flex-end"}
      alignItems={"center"}
      sx={{
        backgroundColor: "white",
      }}
    >
      <Box marginRight={{ xs: 1, sm: 2 }}>
        <Link
          underline="none"
          component="a"
          href="https://www.instagram.com/spacetalk_landscape.design/"
          // color={colorInvert ? "common.white" : "text.primary"}
          sx={{ display: "flex", alignItems: "center" }}
        >
          <img src="/img/instar.png" alt="Instar" />
        </Link>
      </Box>
      <Box marginRight={{ xs: 1, sm: 2 }}>
        <Link
          underline="none"
          component="a"
          href="https://blog.naver.com/space_talk"
          // color={colorInvert ? "common.white" : "text.primary"}
          sx={{ display: "flex", alignItems: "center" }}
        >
          <img src="/img/blog.png" alt="Blog" />
        </Link>
      </Box>
      <Box marginRight={{ xs: 1, sm: 2 }}>
        <Link
          underline="none"
          component="a"
          href="/docs/introduction"
          // color={colorInvert ? "common.white" : "text.primary"}
        >
          Login
        </Link>
      </Box>
    </Box>
  );
};

export default TopNav;
