import React from "react";
import withRoot from "src/withRoot";
import ProductCTA from "./ProductCTA";
import ProductInterior from "./ProductInterior";
import Description from "./Description";
import Box from "@mui/material/Box";
import BrandInfo from "./Brand/BrandInfo";
import WorkInfo from "./Work/WorkInfo";

import Container from "../components/Container";

import Hero from "./Brand/Hero";

const Home = () => {
  return (
    <React.Fragment>
      <Hero />
      <Box bgcolor={"#377dff"}>
        <Container>
          <Description />
        </Container>
      </Box>
      <Container>
        <ProductInterior />
      </Container>
      <Container>
        <BrandInfo />
      </Container>
      <Container>
        <WorkInfo />
      </Container>
      <ProductCTA />
    </React.Fragment>
  );
};

export default withRoot(Home);
